/*
Hamburger menu */
.option a:hover {
  background-color: #cce6ff;
  border-radius: 10px;
  margin-right: 5px;
}

.active {
  background-color: #cce6ff;
  margin-right: 5px;
}

.nav-options {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: -100%;
  margin: 0px;
  transition: all 0.5s ease;
  width: 80vw;
  background-color: #d9d9d9;
}

.option {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10vw;
  padding: 24px 0px;
}

.nav-options.active {
  top: 0;
  left: 0;
  z-index: 5;
  align-content: center;
  width: 100vw;
  margin: 0px;
  padding: 1px;
  transition: all 0.5s ease;
  background-color: #4e008e;
}

.mobile-menu {
  display: block;
  padding-right: 10px;
  margin-left: auto;
}

.menu-icon {
  color: #ffffff;
  position: absolute;
  top: 110px;
  left: 22px;
  z-index: 6;
  width: 45px;
  height: 45px;
}

a {
